import React ,{ Component } from "react"
import { connect } from "react-redux"
import { Link } from 'react-router-dom'

import "./header.scss"

class Header extends Component{

    back(){
        window.history.back(-1);
    }

	render() {
        return (<div className="header">
        	<Link className="menu-link-item" to="/"></Link>
        	<span className="cut-line">/</span>
        	<Link className="menu-link-item" to="/rank"></Link>
        	<span className="cut-line">/</span>
        	<Link className="menu-link-item" to="/playlist"></Link>
        	<span className="cut-line">/</span>
        	<Link className="menu-link-item" to="/new"></Link>
        	<span className="cut-line">/</span>
        	<Link className="menu-link-item" to="/search"></Link>
        	<span className="cut-line">/</span>
        </div>)
    }
}

const mapStateToProps = state => {
    return state
}

export default connect(mapStateToProps)(Header)


