import { fromJS, Map, List } from "immutable"
import {
    TOGGLE_LOADING
} from "../actions"

const INITIAL_STATE = fromJS({
	isLoading:false,
	loadingText:[
		'',
		'',
		'',
		'',
		'',
		'',
		'',
		'',
		'',
		'',
		'',
		'',
		'',
		'',
	],
})

const loadingState = (state = INITIAL_STATE, action) => {
	switch(action.type){
		case TOGGLE_LOADING :
			return state.set("isLoading",action.data)
			break
		default: 
			return state
	}
}
export default loadingState


